import * as React from "react";
import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";
import Typography from "@mui/material/Typography";
import Box from "@mui/material/Box";

interface TabPanelProps {
  children?: React.ReactNode;
  index: number;
  value: number;
}

export interface TabDetail {
  label: string;
  component: React.ReactNode;
}

export interface SimpleListProps {
  tabs: TabDetail[];
  width?: string;
  scrollable?: boolean;
  onClick?: (str: string) => void;
  selected?: number;
}

const TabPanel = (props: TabPanelProps) => {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`vertical-tabpanel-${index}`}
      aria-labelledby={`vertical-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ pt: 2, pr: 2 }}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
};

const a11yProps = (index: number) => {
  return {
    id: `vertical-tab-${index}`,
    "aria-controls": `vertical-tabpanel-${index}`,
  };
};

const SimpleList = ({
  tabs,
  width,
  scrollable,
  selected,
  onClick,
}: SimpleListProps) => {
  const [value, setValue] = React.useState<number>(0);

  React.useEffect(() => {
    selected && setValue(selected);
  }, [selected]);

  const handleChange = (event: React.SyntheticEvent, newValue: number) => {
    setValue(newValue);
  };

  return (
    <Box
      sx={{
        flexGrow: 1,
        bgcolor: "background.paper",
        display: "flex",
        maxHeight: scrollable ? "70vh" : "100%",
      }}
    >
      <Tabs
        orientation="vertical"
        variant="scrollable"
        scrollButtons={false}
        value={value}
        onChange={handleChange}
        aria-label="Vertical tabs"
        sx={{
          borderRight: 1,
          borderColor: "divider",
          mt: 2,
          "& .MuiTabs-indicator": {
            backgroundColor: "red",
          },
        }}
      >
        {tabs.map((item, idx) => (
          <Tab
            sx={{
              width: width ? width : "100%",
              fontSize: "12px",
              padding: "16px",
              margin: "0px",
              "&.Mui-selected": {
                color: "red",
              },
            }}
            label={item.label}
            key={idx}
            {...a11yProps(idx)}
            onClick={(e) => {
              onClick &&
                e.currentTarget.textContent &&
                onClick(e.currentTarget.textContent);
            }}
          />
        ))}
      </Tabs>
      {tabs.map((item, idx) => (
        <TabPanel value={value} key={idx} index={idx}>
          {item.component as React.ReactNode}
        </TabPanel>
      ))}
      {selected && (
        <TabPanel value={value} key={selected} index={selected}>
          {tabs[0].component as React.ReactNode}
        </TabPanel>
      )}
    </Box>
  );
};

export default SimpleList;
