import {
  Box,
  Chip,
  Divider,
  FormControl,
  Grid,
  InputLabel,
  MenuItem,
  Select,
  TextField,
  Typography,
} from "@mui/material";
import TAGS from "../../tags";

export interface RecipeDetailsProps {
  name: string;
  id: string;
  sweetnessLevel: string;
  tags: string[];
  isRecommendation?: boolean;
  isTags?: boolean;
}

export interface ITags {
  [key: string]: string;
}

export const RecipeDetails = ({
  name,
  id,
  sweetnessLevel,
  tags,
  isRecommendation,
  isTags,
}: RecipeDetailsProps) => {
  return (
    <>
      <Grid container xs={12}>
        {isRecommendation ? (
          <Grid item xs={12} sx={{ mt: -2, textAlign: "center" }}>
            <Typography variant="h6" component="h2">
              {name}
            </Typography>
          </Grid>
        ) : (
          <Grid sx={{ mt: -2, pl: 4 }}>
            <Typography variant="h6" component="h2">
              Recipe Details
            </Typography>
          </Grid>
        )}
      </Grid>
      {isRecommendation && (
        <Grid container spacing={2} xs={12} sx={{ pt: 2, pl: isTags ? 0 : 2 }}>
          <Grid item xs={12}>
            <Chip label={`Id: ${id}`} />
          </Grid>
        </Grid>
      )}
      <div
        style={{
          width: "110%",
          height: "420px",
          overflowX: "hidden",
          overflowY: "auto",
        }}
      >
        <Box
          sx={{
            width: 740,
          }}
        >
          {isRecommendation && (
            <Grid
              container
              spacing={2}
              sx={{ mt: 0, pt: -3, pl: isTags ? 0 : 2 }}
              xs={12}
            >
              <Grid item xs={12}>
                <Typography
                  sx={{
                    fontSize: "14px",
                    fontWeight: "bold",
                  }}
                >
                  Tags:-
                  {TAGS.filter((item) => item.recipe_name === name)[0].tags.map(
                    (tag: ITags) => {
                      let i = 0;
                      const keys = Object.keys(tag);
                      keys.map((k) => {
                        const value = tag[k];
                        i += value.split("/").length;
                      });
                      return i;
                    }
                  )}
                </Typography>
                <br />
              </Grid>
            </Grid>
          )}
          <Grid container sx={{ pl: 2 }} xs={12}>
            {isRecommendation ? (
              TAGS.filter((item) => item.recipe_name === name)[0].tags.map(
                (tag: ITags) => {
                  const keys = Object.keys(tag);
                  return keys.map((k) => {
                    const value = tag[k];
                    return (
                      <Grid item xs={6}>
                        <Grid
                          item
                          wrap="nowrap"
                          xs={12}
                          sx={{
                            justifyContent: "flex-end",
                            textAlign: "left",
                            padding: "0px",
                            margin: "0px",
                            fontSize: "14px",
                            paddingRight: 2,
                          }}
                        >
                          {k
                            .split("_")
                            .join(" ")
                            .toLowerCase()
                            .replace(/(?:^|\s)\w/g, function (match) {
                              return match.toUpperCase();
                            })}
                          {value.split("/").map((v) => (
                            <Chip
                              size="small"
                              sx={{
                                float: "right",
                                fontSize: "11px",
                                marginLeft: "1px",
                              }}
                              label={v}
                            />
                          ))}
                        </Grid>
                      </Grid>
                    );
                  });
                }
              )
            ) : (
              <>
                <Grid container xs={12} sx={{ mt: 4, ml: -8 }}>
                  <Grid item xs={4}>
                    <Typography>Recipe Name</Typography>
                  </Grid>
                  <Grid item xs={8}>
                    <TextField
                      label="Recipe Name"
                      value={name}
                      size="small"
                      required
                      variant="outlined"
                      color="secondary"
                      type="text"
                      sx={{ mb: 2 }}
                      fullWidth
                    />
                  </Grid>
                </Grid>
                <Grid container xs={12} sx={{ ml: -10 }}>
                  <Grid item xs={4}>
                    <Typography>Recipe Id</Typography>
                  </Grid>
                  <Grid item xs={8}>
                    <TextField
                      label="Recipe Id"
                      size="small"
                      value={id}
                      required
                      variant="outlined"
                      color="secondary"
                      type="text"
                      fullWidth
                      sx={{ mb: 2, ml: 2 }}
                    />
                  </Grid>
                </Grid>
                <Grid container xs={12} sx={{ ml: -7 }}>
                  <Grid item xs={4}>
                    <Typography>Sweetness Level</Typography>
                  </Grid>
                  <Grid item xs={8} sx={{ ml: -1 }}>
                    <FormControl fullWidth>
                      <InputLabel id="select-label" size="small">
                        {sweetnessLevel}
                      </InputLabel>
                      <Select
                        labelId="select-label"
                        size="small"
                        id="select"
                        label="Recipe Nature"
                        disabled
                      >
                        <MenuItem value={20}>Low</MenuItem>
                        <MenuItem value={30}>Medium</MenuItem>
                      </Select>
                    </FormControl>
                  </Grid>
                </Grid>
              </>
            )}
          </Grid>

          <Divider
            sx={{
              mt: 2,
              height: "0.1px",
              backgroundColor: "red",
              width: "110%",
            }}
          />
          <Grid
            container
            spacing={4}
            sx={{ mt: 0, ml: isRecommendation ? 0 : -9 }}
            xs={12}
          >
            <Grid item xs={12} sx={{ textAlign: "left" }}>
              <Typography>Ingredients</Typography>
              {TAGS.filter((item) => item.recipe_name === name)[0]
                .ingredients.split(";")
                .map((i) => (
                  <Chip size="small" label={i} />
                ))}
            </Grid>
          </Grid>
        </Box>
      </div>
    </>
  );
};
